body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  margin: auto;
  max-width: 900px;
  font-family: "Montserrat", sans-serif;
}
:root {
  --primary-color: #34d399;
  --section-color: #ecfdf5;
}

/* Navigation bar */

.navbar {
  padding: 0.5rem;
  color: white;
  background-color: #34d399;
  background-color: var(--primary-color);
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.navbar-brand {
  font-weight: bold;
  font-size: x-large;
}
.unordered-list {
  text-align: right;
}
#home {
  font-weight: bold;
}
li {
  display: inline;
}
.navbar-item {
  padding: 0rem 1rem 0rem 0rem;
  color: white;
  text-decoration: none;
  font-size: larger;
}

/* Links */
.link1 {
  padding: 0.5rem 1rem;
}
.primary-link {
  border-radius: 0.5rem;
  color: white;
  background-color: #34d399;
  background-color: var(--primary-color);
  text-decoration: none;
}
.secondary-link {
  border-radius: 0.5rem;
  color: #34d399;
  color: var(--primary-color);
  border: 1px solid #34d399;
  border: 1px solid var(--primary-color);
  text-decoration: none;
}

/* Header */
header {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 1.3rem;
  padding: 1rem;
}
.image {
  border-radius: 0.5rem;
  max-width: 80%;
  width: 400px;
  display: block;
  margin: 1rem auto;
}
.header-text {
  text-align: center;
  font-weight: bold;
  font-size: xx-large;
}
.clr {
  color: #34d399;
  color: var(--primary-color);
}
.p1 {
  font-size: larger;
}

/* Footer */
footer {
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
  background-color: #34d399;
  background-color: var(--primary-color);
  padding: 2rem 1rem;
  text-align: center;
}
.foot {
  width: 100%;
  max-width: 600px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer-header {
  margin: 0;
  font-weight: bold;
  font-size: 1.3rem;
  color: white;
  text-align: center;
}
.link {
  color: white;
  box-sizing: border-box;
  text-decoration: none;
  padding: 0.3rem;
}
i:hover{
  opacity: 0.8;
}
.fab {
  font-size: 2rem;
}
ul {
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

/* Section */
.container {
  padding: 0rem 1rem;
}
.container1 {
  padding: 2rem;
}
.container1 h2 {
  text-align: center;
}
.container-white {
  max-width: 600px;
  margin: auto;
}
.container2 {
  border-radius: 1rem;
  padding: 2rem;
  background-color: #ecfdf5;
  background-color: var(--section-color);
}
.container2 h2 {
  text-align: center;
}
.container-og {
  margin: 2rem 1rem;
  max-width: 600px;
  margin: auto;
}

/* Lists */
.container-center {
  padding: 0.5rem;
  padding-bottom: 2rem;
  text-align: center;
}
.container-center-og {
  border-radius: 1rem;
  background-color: #ecfdf5;
  background-color: var(--section-color);
  padding: 0.5rem;
  padding-bottom: 2rem;
  text-align: center;
}

/* Blog */
.hr1 {
  max-width: 35rem;
  margin: auto;
}
.blogtext {
  padding: 0.5rem 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.para {
  text-align: center;
  max-width: 600px;
  padding: 1rem;
  margin: auto;
}
.highlight {
  background-color: #e6ecf5;
}
.note{
  background-color: #ecfdf5;
  padding: 1rem;
  border-left: 5px solid black;
  color: #6B7280;
}

.mark{
  font-size: 1.5rem;
  margin: 0;
  color: #4F46E5;
}
@media screen and (max-width: 500px) {
  .blogs {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }
}


